import axiosInstance from "../interceptor";

export const pagedetailsapi = async (params) => {
  const result = await axiosInstance.get(`pages/${params}`);
  return result?.data;
};

export const pageseoapi = async (params) => {
  const result = await axiosInstance.get(`seo/page/${params}`);
  return result?.data;
};

export async function CountryListApi() {
  const result = await axiosInstance.get(
    `list/country-list?limit=0&offset=0&keyword=&count=0`
  );
  if (result && result.data.status == 1) {
    const statedependent =
      result?.data?.data &&
      result?.data?.data?.map((data) => ({
        label: data.name,
        value: data.countryId,
      }));
    return statedependent;
  }
}
export async function StateListApi() {
  const result = await axiosInstance.get(
    `list/zone?limit=0&offset=0&keyword=&count=0&countryId=0`
  );
  if (result && result.data.status == 1) {
    return result.data.data;
  }
}
