import { notFound } from "next/navigation";
import PageDetail from "@/Components/PageDetail";
import { pagedetailsapi, pageseoapi } from "@/api/serversideaction/countryapi";
import { GetAddonsList } from "@/utilities/cookies";
import createMetadata from "@/Components/Seo/createMetadata";
import { GetToken } from "@/utilities/cookies";

export async function generateMetadata(props) {
  const params = await props.params;
  try {
    const addonList = await GetAddonsList().catch(() => null);

    // If SEO disabled → return empty metadata like your example
    if (!addonList?.seo?.status) {
      return createMetadata({
        title: "",
        description: "",
        keywords: "",
      });
    }

    // Use slug or name
    const pageName = params?.name || params?.slug || "";

    const apiRes = await pageseoapi(pageName).catch(() => null);
    const seoData = apiRes?.data || apiRes;

    // If no SEO data → return empty metadata
    if (!seoData) {
      return createMetadata({
        title: "",
        description: "",
        keywords: "",
      });
    }

    // Build metadata like your example style
    return createMetadata({
      title: seoData.metaTagTitle?.slice(0, 60) || "",
      description: seoData.metaTagDescription?.slice(0, 120) || "",
      keywords: seoData.metaTagKeyword || "",
    });
  } catch (err) {
    console.error("generateMetadata error:", err);

    return createMetadata({
      title: "",
      description: "",
      keywords: "",
    });
  }
}

export default async function page(props) {
  const params = await props.params;
  const [token, pagedetail] = await Promise.all([
    GetToken(),
    pagedetailsapi(params?.name),
  ]);

  if (pagedetail?.message == "invalid page") {
    return notFound();
  }

  return (
    pagedetail?.status == 1 && (
      <PageDetail token={token} pagedetail={pagedetail?.data} />
    )
  );
}
